
@media ( max-width: 1250px ) {

    .contruccion {
        display: none;
    }


    .quienes-somos {
        display: flex;
        width: 100%;
    }

    .texto-siguiente p {
        margin: 0rem 2rem;
    }

    .quienes-somos-text {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin: 1rem 2rem;
        margin-bottom: 0px;
    }

    .quienes-somos-text img {
        display: flex;
        margin-bottom: 2rem;
        width: 32rem;
        height: auto;
        margin-right: 0px;
    }

    .quienes-somos-text p {
        text-align: center;
    }

    .texto-siguiente {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin: 1rem 2rem;
        margin-top: 0px;
    }

    .texto-siguiente p {
        text-align: center;
    }

    .quienes-somos ul {
        display: none;
        gap: 2rem;
    }
    
    .quienes-somos ul li {
        margin: 0rem 1rem;
        margin-top: 1rem;
    }

    .container-gallery {
        margin: 2rem;
    }

    .card-servicio {
        padding-top: 2rem;
    }

    .card-servicio {
        text-align: center;
    }

    .card-servicio .card-title::after {
        width: 16rem;
    
    }

    .card-servicio p::after {
        width: 8rem;
        margin-bottom: -.2rem;
    }

    .container-gallery {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 25rem);
    }

    .content-proyectos-realizados {
        flex-direction: column;
        margin-left: 2rem;
        margin-right: 2rem;
        margin-bottom: 0rem;
    }

    .content-proyectos-realizados.linear {
        margin-top: 0px;
        margin-bottom: 2rem;
    }


    .content-proyectos-realizados .card {
        width: 50rem;
        margin: 2rem;
        border-radius: 6px;
        overflow: hidden;
        background-color: #f9f5f0;
        box-shadow: 0px 1px 10px rgb(0, 0, 0, 0.2);
        cursor: default;
        transition: all 400ms ease;
    }

    .content-proyectos-realizados .card img {
        height: 350px;
        object-fit: cover;
    }

    .container-confiaron {
        justify-content: center;
        align-items: center;
        text-align: center;
        margin-bottom: 4rem;
    }

    .container-confiaron ul {
        width: 100%;
        overflow: hidden;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .container-confiaron ul li img {
        width: 15rem;
        height: auto;
    }

    .container-confiaron ul .swiper {
        margin: 0;
        padding-left: 0; 
        padding-right: 0;
        margin-top: 5rem;
    }

    .close {
        font-size: 5rem;
    }

    .next {
        font-size: 5rem;
    }

    .prev {
        font-size: 5rem;
    }

    .content-formulario {
        text-align: center;
        justify-content: center;
        align-items: center;
        display: flex;
        flex-direction: column;
        gap: 3rem;
        margin: 3rem 2rem;
        margin-top: 2rem;
    }

    .formulario {
        max-width: 550px;
        text-align: center;
        justify-content: center;
        align-items: center;
        margin-left: 2rem;
        margin-right: 2rem;
    }

    .formulario .input-mensaje {
        margin: 0;
        padding: 0;

    }

    .formulario-redes-sociales {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .mensaje-enviado {
        text-align: center;
        justify-content: center;
        align-items: center;
    }

    .content-mensaje {
        width: 550px;
    }

    .content-mensaje p {
        line-height: 2.5rem;
    }

    .quienes-somos-text.servicios p {
        margin-right: 0rem;
    }

    .container-banner {
        display: flex;
        height: 65rem;
        margin-top: 8rem;
    }

    .container-navbar {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .navbar.container p {
        display: flex;
    }

    .container-navbar p {
        display: flex;
        margin-right: 2rem;
        margin-left: 25rem;
        justify-content: end;
        align-items: center;
        cursor: pointer;
        text-decoration: none;
        color: var(--secundary-color);
        font-size: 2.5rem;
        font-weight: 600;
        text-transform: uppercase;
        position: relative;
        transition: all .4s ease;
    }

    .container-navbar img {
        margin: 0;
        margin-left: 2rem;
        margin-right: 25rem;
    }

    .container-navbar p:hover {
        color: #fff;
    }

    .container-navbar p .fa-bars {
        margin-left: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .container-navbar .menu {
        display: none;
    }

    .contadores-principal {
        display: flex;
    }

    .quienes-somos-text img {
        width: 30rem;
        height: 25rem;
    }

    .img-agregada {
        margin: 0rem 2rem;
    }

    .img-agregada img {
        width: 100%;
        height: 30rem;
        object-fit: cover;
    }

    .content-proyectos-realizados {
        margin-top: 0;
        margin-bottom: 0;
    }

    .menu-redes .direccion {
        margin-top: -2rem;
    }
    .content-formulario {
        margin-top: 1rem;
    }

    .quienes-somos-text.servicios-page {
        margin: 1rem 2rem;
    }

    .quienes-somos-text.servicios-page img {
        width: 30rem;
        height: 20rem;
        margin-bottom: 2rem;
    }

    .quienes-somos-text.servicios .imagenes2 img {
        width: 30rem;
        height: 20rem;
        margin-bottom: 2rem;
    }
    .servicios-enlace {
        gap: 0;
    }

    .servicio-responsive {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: 2rem 4rem;
    }

    .servicio-responsive p {
        font-weight: 350;
        font-size: 2rem;
        line-height: 3.5rem;
    }

    .servicio-responsive p span {
        font-weight: bold;
    }

    .quienes-somos-text.servicios p {
        display: none;
    }

    .quienes-somos-text.servico {
        margin-top: 3rem;
    }

    .quienes-somos {
        padding-top: 10rem;
        margin-top: -10rem;
    }
    .container-banner {
        margin-bottom: 3rem;
    }
    .servicios-enlace {
        margin-top: .5rem;
    }

    .container-gallery {
        padding-top: 20rem;
        margin-top: -20rem;
    }

    .dropdown-content-1 {
        display: none;
        flex-direction: column;
        list-style: none;
        border-top: 1px solid #fff;
        border-bottom: 1px solid #fff;
    }

    .dropdown-1-active {
        display: flex;
    }

    .dropdown-content-1 li a {
        cursor: pointer;
        text-decoration: none;
        color: var(--secundary-color);
        font-size: 1.4rem;
        font-weight: 600;
        text-transform: uppercase;
        position: relative;
        transition: all .4s ease;
    }

    .dropdown2 {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .dropdown2 i {
        color: var(--secundary-color);
        font-size: 1.4rem;
        margin-left: 1rem;
        transition: ease all .3s;
    }

    .dropdown2 i:hover {
        color: #fff;
    }


}

