        grid-column: span 2;
    }
    .content-proyectos-realizados {
        flex-direction: column;
        align-items: stretch;
    }
    .content-proyectos-realizados .card {
        width: 50%;
        margin: 1rem auto;
    }
    .container-confiaron ul .swiper {
        margin: 4rem 6rem;
    }

    .container-confiaron ul li img {
        width: 12rem;
        height: 12rem;
    }
    .content-formulario {
        grid-template-columns: 1fr 1fr;
        margin: 3rem 5rem;
    }

    .formulario input {
        font-size: 1.8rem;
    }

    .formulario textarea {
        font-size: 1.8rem;
    }


}



@media (max-width: 480px) {

    .content-banner h1 {
        font-size: 2.5rem;
    }

    .container-banner p {
        font-size: 1.2rem;
    }

    .container-banner span .link {
        padding: 0.6rem 1.5rem;
    }
    .contador_cantidad {
        font-size: 2.5rem;
    }
    
    .contador h2 {
        font-size: 1.5rem;
    }
    .quienes-somos ul li {
        margin: 1rem;
    }

    .quienes-somos ul li img {
        width: 6rem;
        border: 2px solid #1359b6;
    }
    .container-gallery {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(6, 20rem);
    }

    .gallery-img-5, .gallery-img-6 {
        grid-column: span 1;
    }

    .card-servicio .card-title {
        font-size: 2rem;
    }

    .card-servicio .textServicio {
        font-size: 1.2rem;
        padding: 0 1rem;
    }

    .card-servicio .ver-mas {
        font-size: 1rem;
        padding: 0.5rem 2rem;
    }
    .content-proyectos-realizados .card {
        width: 100%;
        margin: 1rem 0;
    }

    .content-proyectos-realizados .card .contenido p {
        font-size: 1.2rem;
    }

    .content-proyectos-realizados .card .contenido h3 {
        font-size: 1.5rem;
    }

    .content-proyectos-realizados .card .contenido a {
        font-size: 1.1rem;
        padding: 0.8rem 1.5rem;
    }
    .container-confiaron ul .swiper {
        margin: 3rem 4rem;
    }

    .container-confiaron ul li img {
        width: 10rem;
        height: 10rem;
    }
    .content-formulario {
        grid-template-columns: 1fr;
        margin: 2rem 3rem;
    }

    .formulario input {
        font-size: 1.6rem;
    }

    .formulario textarea {
        font-size: 1.6rem;
    }
}

