/* -------------------------------------- */
/*          CONFIGURACION GENERAL         */
/* -------------------------------------- */

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap');

:root {
    --primary-color: #001c41;
    --secundary-color: rgb(0, 231, 231);
    --background-color: #f9f5f0;
    --dark-color: #151515;
}

html {
    font-size: 62.5%;
    font-family: 'Quicksand', sans-serif;
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}




/* -------------------------------------- */
/*               UTILIDADES               */
/* -------------------------------------- */

.container {
    max-width: 120rem;
    margin: 0 auto;
}

.heading-1 {
    text-align: center;
    font-weight: 400;
    font-size: 4rem;
    position: relative;
}

.heading-1 span {
    font-weight: bold;
}

.heading-1::after {
    content: '';
    width: 6.5rem;
    height: 3px;
    background-color: #1359b6;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translate( -50%, 50% );
}


/* ---------------------------------------------------------------------------------------------------------------------------- */
/* ---------------------------------------------------------------------------------------------------------------------------- */


/* -------------------------------------- */
/*                 BANNER                 */
/* -------------------------------------- */

.container-banner {
    position: relative;
    height: 100vh;
    margin-top: 0rem; /* Ajusta segn sea necesario */
    overflow: hidden;
  }
  
  .content-banner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    opacity: 0;
    transition: opacity 1s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    text-align: start;
    padding: 2rem;
    padding-left: 20rem;
    
  }
  
  .content-banner.active {
    opacity: 1;
  }
  
  .content-banner h1 {
    font-size: 6.5rem;
    color: #fff;
    font-weight: 700;
    margin-bottom: 1rem;
    text-shadow: 2.5px 2.5px 4.5px rgba(0, 0, 0, 2);
  }
  
  .content-banner p {
    line-height: 3rem;
    color: #ffffffc0;
    font-size: 3rem;
    margin-top: 2rem;
    font-weight: 500;
    text-shadow: 2.5px 2.5px 4.5px rgba(0, 0, 0, 1);
    line-height: 4rem;
  }

    
  .content-banner span {
    margin-top: 4rem;

  }
  
  .content-banner span .link {
    text-decoration: none;
    color: #fff;
    background-color: #1359b6;
    display: flex;
    padding: 1rem 3rem;
    font-size: 1.5rem;
    font-weight: 600;
    text-transform: uppercase;
    border-radius: 3rem;
    cursor: pointer;
    transition: all .1s ease;
  }
  
  .content-banner span .link:hover {
    background-color: #fff;
    color: var(--dark-color);
  }


/* -------------------------------------- */
/*                COUNTER                 */
/* -------------------------------------- */

.contadores-principal {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    margin-bottom: 4.5rem;
}

.contador {
    width: 30%;
    text-align: center;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
}


.contador_cantidad {
    color: #114a96;
    font-size: 5.5rem;
    margin: 1rem 0;
    font-weight: 1000;
}

.contador h2 {
    font-size: 2.5rem;
}

.contador .fa-facebook-square {
    color: rgb( 66, 103, 178 );
}

.contador .fa-youtube {
    color: red;
}

.ocultar {
    opacity: 0;
}

.animar {
    animation: aparecer 1.3s;
}

@keyframes aparecer {
    0% {
        opacity: 0;
        transform: translateY( 10rem );
    }

    100% {
        opacity: 1;
        transform: translateY( 0 );
    }
}  




