/* -------------------------------------- */
/*         PROYECTOS REALIZADOS           */
/* -------------------------------------- */

.proyectos-realizados {
    padding-top: 11rem;
    margin-top: -11rem;
}

.content-proyectos-realizados {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}

.linear {
    margin-bottom: 4rem;
} 

.content-proyectos-realizados .card {
    width: 70%;
    margin: 2rem;
    border-radius: 6px;
    overflow: hidden;
    background-color: #f9f5f0;
    box-shadow: 0px 1px 10px rgb(0, 0, 0, 0.2);
    cursor: default;
    transition: all 400ms ease;
}

.content-proyectos-realizados .card:hover {
    box-shadow: 5px 5px 20px rgb(0, 0, 0, 0.4);
    transform: translateY(-3%);
}

.content-proyectos-realizados .card img {
    width: 100%;
    height: 250px;
    cursor: pointer;
    transition: ease .4s all;
}

.card figure {
    position: relative;
}

.card figure img {
    transition: all 0.3s ease;
    z-index: -1;
}

.barra-antipanico {
    image-rendering: -webkit-optimize-contrast; /* Mejora la calidad de la imagen en WebKit (Safari, Chrome) */
    image-rendering: -moz-crisp-edges;          /* Mejora la calidad de la imagen en Firefox */
    image-rendering: -o-crisp-edges;            /* Mejora la calidad de la imagen en Opera */
    image-rendering: crisp-edges; 
}

.card figure:hover img {
    filter: brightness(50%);
}

.card figure::before {
    content: '\f06e';
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3rem;
    color: #fff;
    opacity: 0;
    transition: opacity 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.card figure:hover::before {
    opacity: 1;
    z-index: 1;
}

.content-proyectos-realizados .card .contenido {
    padding: 1.5rem;
    text-align: center;
}

.content-proyectos-realizados .card .contenido p {
    font-size: 1.5rem;
    line-height: 1.5;
    color: #6a6a6a;
    margin-bottom: 1rem;
}

.content-proyectos-realizados .card .contenido h3 {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.content-proyectos-realizados .card .contenido a {
    text-decoration: none;
    display: inline-block;
    padding: 1rem 2rem;
    margin-top: 1rem;
    background-color: #1359b6;
    font-size: 1.3rem;
    font-weight: 600;
    border-radius: 20px;
    color: #fff;
    transition: all .4s ease;
}

.content-proyectos-realizados .card .contenido a:hover {
    color: #1359b6;
    background-color: #fff;
}




/* -------------------------------------- */
/*                GALERIA                 */
/* -------------------------------------- */

.image-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 10px;
}
  
.image-grid img {
    width: 100%;
    cursor: pointer;
}
  
.lightbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    z-index: 3;
}
  
.lightbox img {
    max-width: 1600rem;
    max-height: 50rem;
    object-fit: cover;
}
  
.close, .prev, .next {
    text-align: center;
    justify-content: center;
    align-items: center;
    position: absolute;
    background: none;
    border: none;
    color: white;
    font-size: 3rem;
    cursor: pointer;
    padding: .5rem 1.1rem;
    border-radius: 50%;
    transition: all .5s ease;
}

.close {
    top: 5px;
    right: 10px;
    transition: all .5s ease;
    padding: .5rem 1rem;
    padding-bottom: .3rem;

}
    
.close:hover {
    background-color: red;
}
    
.prev {
    left: 20px;
    transition: all .5s ease;
    padding: .5rem 1.3rem;
    padding-top: .7rem;
}

.prev:hover {
    background-color: #1359B6;
}
  
.next {
    right: 20px;
    transition: all .5s ease;
    padding: .5rem 1.3rem;
    padding-top: .7rem;
}

.next:hover {
    background-color: #1359B6;
}
  
.thumbnail-grid {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 30px;
}

.thumbnail-grid img {
    object-fit: fill;
}
  
.thumbnail {
    width: 60px;
    height: 60px;
    margin: 0 5px;
    cursor: pointer;
    opacity: 0.6;
    border: 2px solid transparent;
}
  
.thumbnail.selected {
    border: 2px solid white;
    opacity: 1;
}

.deleted {
    display: none;
    border-radius: 100%;
    border: 2px solid #fff;
    background-color: #fff;
    width: 4.5rem;
    height: 4.5rem;
}




