    justify-content: center;
    align-items: center;
    text-align: center;
}

.mensaje-enviado.active {
    display: flex;
}

.content-mensaje {
    padding: 2rem;
    background-color: #f0f4f7;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: auto;
}

.content-mensaje h3 {
    font-size: 24px;
    color: #333;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
}

.content-mensaje h3::after {
    content: '';
    width: 3rem;
    height: 3px;
    background-color: #1350a0;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translate( -50%, 50% );
}

.content-mensaje p {
    font-size: 19px;
    color: #555;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
    line-height: 3.5rem;
}

.content-mensaje ul {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    margin-top: 20px;
    margin-bottom: 4rem;
}

.content-mensaje ul li {
    list-style: none;
}

.content-mensaje ul li a {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: #1359b6;
    color: #fff;
    border-radius: 50%;
    transition: background-color 0.3s ease;
}

.content-mensaje ul li a:hover {
    background-color: #000069;
}

.content-mensaje ul li a i {
    font-size: 20px;
    color: #fff;
}

.content-mensaje span a {
    text-decoration: none;
    font-size: 1.6rem;
    font-weight: 500;
    background-color: #1359b6;
    padding: .8rem 2rem;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    transition: ease .4s all;
}

.content-mensaje span a:hover {
    background-color: #000069;
}

.direccion {
    margin-bottom: .8rem;
}

.container-soporte {
    display: flex;
    margin: 0rem 15rem;
    margin-right: 17rem;
    margin-top: 2rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #f0f4f7;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 2rem;
}

.container-soporte p {
    font-size: 1.8rem;
    font-weight: bold;
    color: #000063;
}

.menu-galeria {
    display: none;
}

